import { Box, Card, CardContent } from "@mui/material";
import "./App.css";
import Body from "./components/Body";
import FormBuilder2 from "./components/FormBuilder2";
import Header from "./components/Header";
import Particle from "./components/Particles";
import SurveyChoice from "./pages/SurveyChoice";
import { makeStyles, useTheme } from "@mui/styles";
import illustration from "./assets/survey-illustration.png";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    padding: theme.spacing(3),
    background: "#263859",
    // backgroundImage: `url(${wallpaper})`,
    backgroundSize: "cover",

    minHeight: "100vh",
  },
  text: {
    color: "red",
  },
  paragraph: {
    fontSize: "1.2rem",
    [theme.breakpoints.up("md")]: {
      fontSize: "1.5rem",
    },
    [theme.breakpoints.up("lg")]: {
      fontSize: "2rem",
    },
  },
  illu: {
    position: "fixed",
    height: "20vh",
    bottom: 0,
    right: 0,
    animation: "$move 3s ", // add animation
  },
  "@keyframes move": {
    "0%": {
      right: "100%", // start from the right
    },
    "100%": {
      right: 0, // move towards 0
    },
  },
}));
function App() {
  const classes = useStyles();
  const theme = useTheme();
  return (
    <Box
      sx={{
        background: "#263859",
        minHeight: "100vh",
      }}
    >
      <div className={classes.illustration}>
        <img src={illustration} className={classes.illu} />
      </div>
      <Particle />
      <Header />
      <SurveyChoice />

      {/* <Body /> */}
    </Box>
  );
}

export default App;
