import { Box, Container, Typography } from "@mui/material";
import React from "react";
import FormBuilder2 from "../components/FormBuilder2";
import FormBuilder3 from "../components/FormBuilder3";
import Header from "../components/Header";
import nbev from "../assets/nbev-consumer.json";
import Particle from "../components/Particles";

function NbevSurvey() {
  return (
    <Box sx={{ 
      background: "#263859",

     }}>
      <Particle />

      <Header />
      <Container maxWidth="md" sx={{ marginTop: "2rem", marginBottom: "2rem" }}>
        <Box sx={{ p: "2rem", boxShadow: "0 2px 8px rgba(0, 0, 0, 0.15)", borderRadius: "1rem", backgroundColor: "#FFFFFF" }}>
          <Typography variant="h4" sx={{ textAlign: "center", marginBottom: "2rem" }}>Nbev survey</Typography>
          {/* <FormBuilder2 /> */}
          <FormBuilder3 questions={nbev} />
        </Box>
      </Container>
    </Box>
  );
}

export default NbevSurvey;
