function SurveyChoice() {
  return (
    <Box
      sx={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        minHeight: "60vh",
        width: "100%",
      }}
    >
      <Box sx={{ width: "90%", background: "#263859" }}>
        <Grid container spacing={4} justifyContent="center">
          <Grid item xs={12} sm={6} md={4} lg={3}>
            <Link to="/nbev-survey" style={{ textDecoration: "none" }}>
              <Card
                sx={{
                  height: "40vh",
                  backgroundColor: "lightblue",
                  transition: "transform 0.2s",
                  ":hover": {
                    transform: "scale(1.05)",
                    rotate: "1deg",
                  },
                }}
              >
                <CardContent
                  sx={{
                    padding: 0,
                    margin: 0,
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    flexDirection: "column",
                    textAlign: "center", // Add this property
                    height: "100%",
                  }}
                >
                  <Typography variant="h5">
                    Nbev <br /> Consumer
                  </Typography>
                </CardContent>
              </Card>
            </Link>
          </Grid>
          <Grid item xs={12} sm={6} md={4} lg={3}>
            <Link to="/abinbev-survey" style={{ textDecoration: "none" }}>
              <Card
                sx={{
                  height: "40vh",
                  backgroundColor: "lightblue",
                  transition: "transform 0.2s",
                  ":hover": {
                    transform: "scale(1.05)",
                    rotate: "1deg",
                  },
                }}
              >
                <CardContent
                  sx={{
                    padding: 0,
                    margin: 0,
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    flexDirection: "column",
                    textAlign: "center", // Add this property
                    height: "100%",
                  }}
                >
                  <Typography variant="h5">ABInBev</Typography>
                </CardContent>
              </Card>
            </Link>
          </Grid>
          <Grid item xs={12} sm={10} md={4} lg={3}>
            <Link to="/path/to/destination" style={{ textDecoration: "none" }}>
              <Card
                sx={{
                  height: "40vh",
                  backgroundColor: "lightblue",
                  transition: "transform 0.2s",
                  ":hover": {
                    transform: "scale(1.05)",
                    rotate: "1deg",
                  },
                }}
              >
                <CardContent
                  sx={{
                    padding: 0,
                    margin: 0,
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    flexDirection: "column",
                    textAlign: "center", // Add this property
                    height: "100%",
                  }}
                >
                  <Typography variant="h5">Template 3</Typography>
                </CardContent>
              </Card>
            </Link>
          </Grid>
        </Grid>
      </Box>
    </Box>
  );
}

export default SurveyChoice;
