function FormBuilder() {
  const [currentQuestion, setCurrentQuestion] = useState("S1");
  const [responses, setResponses] = useState({});

  const handleResponse = (id) => {
    setResponses({ ...responses, [currentQuestion]: id });
    const nextQuestion = survey[currentQuestion].responses.find(
      (response) => response.id === id
    ).next;
    setCurrentQuestion(nextQuestion);
    console.log(responses);
  };

  return (
    <Container maxWidth="sm">
      <Box sx={{ mt: 2 }}>
        <Typography variant="h4" align="center" gutterBottom>
          Survey Form
        </Typography>
        {currentQuestion === "Close" ? (
          <Typography variant="body1" align="center">
            Thank you for participating in the survey.
          </Typography>
        ) : (
          <Box>
            <Typography variant="h6" gutterBottom>
              {survey[currentQuestion].question}
            </Typography>
            {survey[currentQuestion].responses.map((response) => (
              <Box key={response.id} sx={{ my: 1 }}>
                <Button
                  variant="contained"
                  onClick={() => handleResponse(response.id)}
                >
                  <Typography  variant="p" color="white">{response.response}</Typography>
                </Button>
              </Box>
            ))}
          </Box>
        )}
      </Box>
    </Container>
  );
}

export default FormBuilder;
