import React, { useState } from "react";
import { makeStyles } from "@mui/styles";
import {
  TextField,
  RadioGroup,
  FormControlLabel,
  Radio,
  Checkbox,
  FormGroup,
  FormControl,
  FormLabel,
  Button,
} from "@mui/material";
import data from "../assets/nbev-consumer.json";

const useStyles = makeStyles((theme) => ({
  form: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    margin: theme.spacing(3),
  },
  field: {
    margin: theme.spacing(2),
  },
  button: {
    margin: theme.spacing(2),
  },
}));

function FormBuilder2() {
  const classes = useStyles();
  const [formData, setFormData] = useState({});

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevFormData) => ({ ...prevFormData, [name]: value }));
  };

  const handleCheckboxChange = (e) => {
    const { name, checked } = e.target;
    setFormData((prevFormData) => ({
      ...prevFormData,
      [name]: checked ? true : false,
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log(formData);
  };

  const formFields = Object.values(data).map((fieldData) => {
    const { id, section, question, subtext, choice, required, min, type } =
      fieldData;

    switch (type) {
      case "radio":
        return (
          <FormControl key={id} component="fieldset" className={classes.field}>
            <FormLabel component="legend">{question}</FormLabel>
            <RadioGroup
              name={id}
              value={formData[id] || ""}
              onChange={handleInputChange}
            >
              {choice.map((option, index) => (
                <FormControlLabel
                  key={index}
                  value={option}
                  control={<Radio />}
                  label={option}
                />
              ))}
            </RadioGroup>
          </FormControl>
        );
      case "check":
        return (
          <FormControl key={id} component="fieldset" className={classes.field}>
            <FormLabel component="legend">{question}</FormLabel>
            <FormGroup>
              {choice.map((option, index) => (
                <FormControlLabel
                  key={index}
                  control={
                    <Checkbox
                      name={option}
                      checked={formData[option] || false}
                      onChange={handleCheckboxChange}
                    />
                  }
                  label={option}
                />
              ))}
            </FormGroup>
          </FormControl>
        );
      case "text":
        return (
          <TextField
            key={id}
            className={classes.field}
            label={question}
            name={id}
            value={formData[id] || ""}
            onChange={handleInputChange}
            multiline
            rows={4}
            variant="outlined"
          />
        );
      default:
        return null;
    }
  });

  return (
    <form onSubmit={handleSubmit} className={classes.form}>
      {formFields}
      <Button
        type="submit"
        variant="contained"
        color="primary"
        className={classes.button}
      >
        Submit
      </Button>
    </form>
  );
}
export default FormBuilder2;
